.section_selected {
	position: relative;

	width: 100%;
	background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 0) 100%);

	padding: rem(120) 0;

	&::after {
		content: "";
		position: absolute;
		top: 0;
		margin-top: rem(-120);
		right: 0;

		width: rem(320);
		height: rem(320);
		background: url(../img/timer.png) no-repeat top right;

		@include tablet {
			width: rem(200);
			height: rem(200);
			background-size: rem(200);
		}
	}

	.text-title {
		text-align: center;
		margin-bottom: rem(80);
	}

	.text-title-50 {
		margin-bottom: rem(24);
	}

	.item_select {
		display: flex;
		flex-wrap: wrap;
		border-radius: rem(14);
		padding-top: rem(32);
		padding-right: rem(24);
		box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);

		width: calc(50% - 0.625rem);
		height: rem(312);
		margin-bottom: rem(62);

		&-left {
			position: relative;
			flex: 0 0 40%;

			img {
				position: absolute;
				top: rem(-30);
				left: 0;
				width: 100%;
			}
		}

		&-right {
			flex: 0 0 60%;

			.title {
				font-weight: 600;
				font-size: rem(20);
				line-height: rem(30);
				margin-bottom: rem(12);
				display: block;
			}

			.text {
				color: #80827F;
				font-size: rem(14);
				line-height: rem(20);
				margin-bottom: rem(34);
				max-width: rem(330);
			}
		}
	}
}
.block_choice-wrap {
	position: relative;
	width: rem(413);

	&::before {
		content: "";
		position: absolute;
		top: rem(-93);
		left: rem(-76);
		width: rem(166);
		height: rem(146);

		background: url(../img/dumbbells.png) no-repeat;
		z-index: -1;
	}
	
}
.block_choice {
	position: relative;
	
	border-radius: rem(14);
	padding: rem(67) rem(51) rem(67) rem(40);
	background: rgba(255, 255, 255, 0.8);
	/* Shadow+Blur */

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	backdrop-filter: blur(16px);
	// filter: blur(16px);

	&::after {
		content: "";
		position: absolute;
		// top: rem(-93);
		bottom: rem(-46);
		right: rem(-52);
		width: rem(171);
		height: rem(175);

		background: url(../img/heart.png) no-repeat;
		// z-index: -1;
	}

	&-item {
		display: flex;
		// flex-wrap: wrap;
		margin-bottom: rem(40);

		&:last-child {
			margin-bottom: 0;
		}

		.choice_item-image {
			flex: 0 0 rem(80);
			width: rem(80);
			height: rem(80);
			border-radius: rem(20);
			overflow: hidden;
			margin-right: rem(24);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.choice_descr {
			flex: 0 0 1;
			.title {
				font-size: rem(24);
				line-height: rem(30);
				font-weight: 600;
				display: block;
				margin-bottom: rem(16);
			}

		}
	}
}

.button-plus {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	line-height: rem(22);
	font-weight: 600;
	color: $pink;

	.plus {
		margin-right: rem(8);
		border-radius: 50%;
		width: rem(15);
		height: rem(15);
		border: rem(1.5) solid $pink;
		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(9);
			font-weight: 600;
			color: $pink;
		}
	}
}

.block_choice-right {
	flex: 1 0 0;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}