.header {
	// position: sticky;
	// top:0;
	// z-index: 50;

	background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 0) 100%);

	> .container {
		padding: rem(24) 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}
		

	&_logo {
		margin-right: auto;
		a {
			max-width: rem(122);
			width: 100%;
			display: block;
		}
	}

	&_nav {
		margin-right: rem(122);
		ul {
			display: flex;
			flex-wrap: wrap;
			li {
				margin-right: rem(48);
				display: flex;
				align-items: center;
				&:last-child {
					margin-right: 0;
				}
				i {
					font-size: rem(10);
					margin-left: rem(10);
				}
				a {
					font-weight: 600;
				}
			}
		}
	}
}