.section_faq {

	.block_faq {
		margin-top: rem(80);

		&-item {
			display: flex;
			flex-direction: column;

			.faq_item-header {
				cursor: pointer;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;
				padding: rem(32) 0;
				border-bottom: rem(1) solid #F2E7DC;
				transition: all 300ms ease;

				.title {
					font-size: rem(20);
					line-height: 120%;
					font-weight: 600;
				}
			}

			.faq_item-text {
				line-height: 140%;
				color: #80827F;
				width: 54%;
				display: none;
			}

			&:hover {
				.button-icon {
					background-color: $pink;

					i {
						&::before {
							display: none;
						}

						&::after {
							content: "";
							height: rem(2);
							width: rem(16);
							background-color: white;
							display: block;
							border-radius: rem(3);
						}
					}
				}
			}
		}

		&-item.active {
			.faq_item-header {
				padding: rem(32) 0 rem(22) rem(0);
			}
			.faq_item-text {
				display: block;
			}

			.button-icon {
				background-color: $pink;

				i {
					&::before {
						display: none;
					}

					&::after {
						content: "";
						height: rem(2);
						width: rem(16);
						background-color: white;
						display: block;
						border-radius: rem(3);
					}
				}
			}
		}
	}
}