.section_start {
	position: relative;

	.container {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			top: rem(-60);
			right: rem(-227);
			width: rem(1018);
			height: rem(958);
			// height: 100%;
			object-fit: cover;
			// opacity: .05;
			background: url('../img/header_bg.png') no-repeat;
			z-index: -1;
		}

		.photo_card {
			position: absolute;
			bottom: 0;
			right: rem(-72);
		}
	}

	
	.title {
		font-size: rem(64);
		line-height: rem(64);
		font-weight: 600;
		margin-bottom: rem(32);

		padding-top: rem(158);

		span {
			color: $pink;
		}
	}

	.text {
		font-size: rem(20);
		font-weight: 600;
		margin-bottom: rem(48);
	}

	&-buttons {
		display: flex;
		flex-wrap: wrap;

		.button-default {
			margin-right: rem(16);
		}
	}

	.line {
		margin-top: rem(100);
	}

	.block_advach {
		margin-top: rem(128);
	}

	// &-right {
	// 	position: absolute;
	// 	top: 0;
	// 	right: 0;
	// 	z-index: -1;
	// 	opacity: .2;
	// 	width: rem(554);
	// }
}

.block_advach {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	width: rem(244);
	padding: rem(118) rem(32) rem(32) rem(32);

	border-radius: rem(14);
	box-shadow: 0px 24px 32px rgba(209, 194, 194, 0.14),
	0px 24px 56px rgba(209, 194, 194, 0.12);

	&-image {
		position: absolute;
		top: rem(-20);
		left: rem(-10);
		width: rem(266);
	}

	&-item {
		margin-bottom: rem(30);

		&:last-child {
			margin-bottom: 0;
		}
		.title {
			color: $pink;
			font-size: rem(56);
			line-height: rem(64);
			margin-bottom: rem(20);
			// display: block;
		}
		.text {
			font-size: rem(18);
			line-height: 120%;
			color: #80827F;
			margin-bottom: 0;
		}
	}
}

.photo_card {
	width: rem(112);
	height: rem(112);
	border-radius: 50%;
	border: rem(6) solid white;
	// transform: matrix(1, 0, 0, -1, 0, 0);
	box-shadow: 0px 11px 60px rgba(0, 0, 0, 0.1);
	overflow: hidden;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}