* {
	outline: none;
	border: 0;
}

html {
	font-size: 100%;
	height: 100%;

	@include max {
		font-size: 140%;
	}
	@include hd {
		font-size: 180%;
	}
	@include w1200_w900 {
		font-size: 80%;
	}
	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: 'Gilroy';
	font-weight: 400;
	font-size: rem(16);
	color: #3D4039;
	// height: 100%;
	overflow-x: hidden;
}

.relative {
	position: relative;
}
.container-fluid {
	position: relative;
	width: 100%;
	margin: 0 auto;
}
.container {
	width: 1280px;
	margin: 0 auto;

	// @include max {
	// 	width: 1300px;
	// }

	@include w1200_w900 {
		width: 900px;
	}

	@include min {
		width: calc(100% - 30px);
	}

	@include max {
		width: 1600px;
	}

	@include hd {
		width: 2400px;
	}
	@include w1200_w900 {
		width: 900px;
	}
}

.grid-col-right {
	display: grid;
	grid-template-columns: calc((100% - 1280px)/2) 4.3fr 5.7fr calc((100% - 1280px)/2);

	@include max {
		grid-template-columns: calc((100% - 1600px)/2) 4.3fr 5.7fr calc((100% - 1600px)/2);
	}

	@include hd {
		grid-template-columns: calc((100% - 2400px)/2) 4.3fr 5.7fr calc((100% - 2400px)/2);
	}

	@include w1200_w900 {
		grid-template-columns: calc((100% - 900px)/2) 4.3fr 5.7fr calc((100% - 900px)/2);
	}
}


.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}


// grid

.w-6\/12 {
	width: calc(50% - 0.625rem);
}
.w-4\/12 {
	width: calc(33.333% - 0.625rem);
}
.w-3\/12 {
	width: calc(25% - 0.625rem);
}

.text-title {
	font-size: rem(48);
	line-height: rem(56);
	font-weight: 700;

	span {
		color: $pink;
	}
}
.text-semi {
	font-weight: 600 !important; 
}

.button-i {
	border-radius: rem(12);
	padding: rem(4) rem(4) rem(4) rem(16);
	box-shadow: 0px 4px 8px rgba(218, 90, 135, 0.12),
	0px 16px 32px rgba(218, 90, 135, 0.08);
	color: $pink;
	font-weight: 600;
	display: flex;
	align-items: center;

	.border {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: rem(12);
		background-color: $pink;
		width: rem(40);
		height: rem(40);
		margin-left: rem(16);

		i {
			color: white;
			font-size: rem(24);
		}
	}
}

.button-default {
	display: inline-block;
	padding: rem(16) rem(30) rem(18) rem(30);
	// height: rem(56);
	border-radius: rem(14);
	background-color: $pink;
	color: white;
	font-weight: 600;
	box-shadow: 0px 4px 8px rgba(218, 90, 135, 0.12),
	0px 16px 32px rgba(218, 90, 135, 0.08);

	transition: all 300ms ease;

	&:hover {
		background-color: white;
		color: $pink;
		box-shadow: 0px 4px 8px rgba(218, 90, 135, 0.12),
		0px 16px 32px rgba(218, 90, 135, 0.08);
	}
}

.button-default-b {
	padding: rem(13) rem(20);
	border-radius: rem(14);
	border: rem(1) solid $pink;
	font-weight: 600;
	color: $pink;
	background: rgba(218, 90, 135, 0.1);
	display: inline-block;
}

.button-white {
	display: inline-block;
	padding: rem(16) rem(30) rem(18) rem(30);
	border-radius: rem(14);
	background-color: white;
	color: $pink;
	font-weight: 600;
	box-shadow: 0px 4px 8px rgba(218, 90, 135, 0.12),
	0px 16px 32px rgba(218, 90, 135, 0.08);
}

.button-icon {
	width: rem(40);
	height: rem(40);
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(218, 90, 135, 0.1);

	i {
		font-size: rem(16);
		color: $pink;
	}
}

.line {
	width: rem(158);
	height: rem(18);
	background-size: 100%;
	background: url(../img/header_line.png) no-repeat;
}

.mt-60 {
	margin-top: rem(60);
}
.mt-80 {
	margin-top: rem(80);
}

.text-title-50 {
	width: 60%;
	text-align: center;
}


.block-top-arrow {
	position: relative;
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: rem(-61);
		width: rem(122);
		height: rem(32);
		background: url(../img/block_top_arrow.svg) no-repeat;
	}
}

.text-pink {
	color: $pink;
}