.footer {
	position: relative;
	margin-top: rem(120);
	padding: rem(60) 0;
	// padding-bottom: ;
	// background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 0) 100%);
	border-top: rem(1) solid #F2E7DC;

	.button-icon {
		position: absolute;
		top: rem(-20);
		left: 50%;
		margin-left: rem(-20);
		z-index: 1;

		background-color: white;

		border: rem(1) solid #F2E7DC;

		i {
			font-size: rem(10);
		}

		&:hover {
			border-color: $pink;
			background-color: $pink;

			i {
				color: white;

				&::before {
					color: white;
				}
			}
		}
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		
	}

	&_logo {
		max-width: rem(220);
		margin-right: rem(105);
	}

	&_nav {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		width: rem(358);

		ul {
			flex: 0 0 50%;

			li {
				margin-bottom: rem(16);
				font-weight: 600;

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-weight: 600;
				}
			}
		}
	}

	&_social {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		flex: 1 0 0;

		a {
			margin-right: rem(16);
			display: flex;
			align-items: center;
			justify-content: center;

			background: rgba(222, 214, 207, 0.3);
			border-radius: 50%;

			width: rem(40);
			height: rem(40);

			i {
				color: $pink;
				font-size: rem(16);
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&_copy {
		margin-left: auto;

		ul {
			text-align: right;

			li {
				margin-bottom: rem(22);
				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(12);
					color: #797979;
					line-height: 115%;
					text-decoration: underline;

					&:hover {
						text-decoration: none;
					}
				}
			}
		}
	}
}