.section_about {
	position: relative;
	width: 100%;
	background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 0) 100%);
	padding: rem(120) 0;

	

	.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;

		&::before {
			content: "";
			position: absolute;
			top: rem(-182);
			right: rem(-147);

			width: rem(347);
			height: rem(376);
			background: url(../img/weight_plate.png) no-repeat;
		}


	}

	&-right {
		margin-left: rem(100);

		.line {
			margin-bottom: rem(40);
		}

		.sect-title {
			font-size: rem(48);
			line-height: rem(56);
			font-weight: 700;
			display: block;
			margin-bottom: rem(30);

			span {
				color: $pink;
			}
		}

		.text-first {
			font-size: rem(24);
			line-height: rem(34);
			color: #80827F;
			margin-bottom: rem(10);
		}
		.text-last {
			color: #80827F;
			line-height: rem(22);
			margin-bottom: rem(40);
		}
	}

	&-bottom {
		margin-top: rem(80);
		display: flex;
		flex-direction: column;
		align-items: center;

		p {
			font-size: rem(24);
			line-height: rem(34);
			margin-bottom: rem(31);
			width: 60%;
		}


	}
}

.image-text {
	display: flex;
	flex-wrap: wrap;

	&_img {
		flex: 0 0 rem(80);
		height: rem(80);
		margin-right: rem(24);

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	p {
		flex: 1 0 0;
		font-size: rem(20);
		line-height: rem(30);
		color: #80827F;
	}
}

.block-sertificate {
	position: absolute;
	right: 0;
	bottom: 0;
	width: rem(237);
	padding: rem(32) rem(20);
	overflow: hidden;
	border-radius: rem(12);
	text-align: center;

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	backdrop-filter: blur(16px);
	background: rgba(255, 255, 255, 0.8);

	.title {
		font-size: rem(20);
		font-weight: 600;
		line-height: 120%;
		margin-bottom: rem(10);
		display: block;
	}
	.text {
		line-height: 120%;
		margin-bottom: rem(24);
	}

}