.section_tabs {

	padding-top: rem(178);
	margin-bottom: rem(120);

	.text-title {
		margin-bottom: rem(52);
		text-align: center;
	}

	&-nav {
		display: flex;
		justify-content: space-between;
		// overflow-x: scroll;
		border-bottom: rem(1) solid #DED6CF;
		margin-bottom: rem(40);

		button {
			font-weight: 500;
			color: #A4A4A4;
			padding-top: rem(10);
			padding-bottom: rem(24);
			font-size: rem(16);
			position: relative;
			// margin-right: rem(40);
			transition: all 400ms ease;
			white-space: nowrap;

			// &:last-child {
			// 	margin-right: 0;
			// }

			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: rem(1);
				background-color: $pink;
				opacity: 0;
				transition: all 400ms ease;
			}

			&:hover {
				color: $pink;

				&::after {
					opacity: 1;
				}
			}

			
		}
		button.active {
			color: $pink;

			&::after {
				opacity: 1;
			}
		}
	}

	&-item {
		display: flex;
		flex-wrap: wrap;

		.swiper-container {
			width: 100%;
			height: rem(510);
		}

		.swiper-slide {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			align-items: center;
		}

		.tabs_item-left {
			position: relative;
			width: rem(660);
			height: rem(510);

			.box-image {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
			}

			.box-text {
				position: absolute;
				bottom: 0;
				right: 0;
				padding: rem(16);
				border-radius: rem(12);
				background-color: white;
				z-index: 10;
				max-width: rem(180);

				box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
				0px 16px 32px rgba(209, 194, 194, 0.08);

				> span {
					color: #80827F;
					line-height: rem(22);
					font-weight: 600;

					span {
						color: $pink;
						text-transform: uppercase;
					}
				}
			}
		}

		.tabs_item-right {
			display: flex;
			// align-items: center;
			// flex-direction: column;
			align-items: center;
			justify-content: space-between;
			flex-grow: 1;

			.box {
				max-width: rem(317);
				margin-left: rem(100);
				.border-text-b {
					margin-bottom: rem(24);

				}

				.title {
					font-size: rem(32);
					font-weight: 600;
					margin-bottom: rem(24);
				}

				.list {
					margin-bottom: rem(48);
				}
				
			}

			

			
		}

		.arrow_control {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			max-width: rem(40);
			position: absolute;
			top: 50%;
			margin-top: rem(-86);
			right: 0;
			z-index: 10;

			.arrow-top {
				margin-bottom: rem(46);
			}

			.arrow-bottom {
				margin-top: rem(46);
			}

			.arrow_control-nav {
				display: flex;
				flex-direction: column;

				.swiper-pagination-bullet-active {
					width: rem(8) !important;
					height: rem(8) !important;
					border-radius: 50%;
					background-color: white !important;
					border: rem(2) solid $pink;
				}

				.swiper-pagination-bullet {
					width: rem(6);
					height: rem(6);
					border-radius: 50%;
					background-color: #DED6CF;
					margin-bottom: rem(12);
					opacity: 1 !important;
					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}

		
	}
}

.list {
	li {
		position: relative;
		padding-left: rem(20);
		font-size: rem(16);
		color: #80827F;
		line-height: 120%;
		margin-bottom: rem(16);

		&:last-child {
			margin-bottom: 0;
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(6);
			left: 0;
			width: rem(8);
			height: rem(8);
			border-radius: 50%;
			background-color: $pink;
		}
	}
}

.border-text-b {
	display: inline-block;
	font-size: rem(11);
	font-weight: 700;
	color: $pink;
	line-height: rem(16);
	padding: rem(7) rem(12);
	border: rem(1) solid $pink;
	border-radius: rem(8);
}

.arrow-button {
	width: rem(40);
	height: rem(40);
	border-radius: 50%;
	border: rem(1) solid #DED6CF;

	display: flex;
	align-items: center;
	justify-content: center;

	i {
		font-size: rem(10);
		color: $pink;

		&::before {
			color: $pink;
		}
	}
}